import uniSection from '@/components/uni-section/uni-section.vue';
import uniLoadMore from '@/components/uni-load-more/uni-load-more.vue';
import empty from "@/components/empty";
import uniRequest from 'uni-request';
import Json from '@/Json';
import unikModal from '@/components/unik-modal/unik-modal.vue'
export default {
	components: {
		uniLoadMore,
		empty,
		// unikModal,
		uniSection
	},
	data() {
		return {
			modalTitle: '退/换货',
			
			tabCurrentIndex: 0,
			orderList:[],
			navList: [],
		};
	},
	
	onLoad(options){
		this.getDate()
	},
    onShow(){
		 
	},
	methods: {
		async getDate(){
			await uniRequest({
				url:'mobile/order/list/return/goods/user/',
				method:'get',
				headers:{
					Authorization:'JWT '+uni.getStorageSync('userInfo').token
				},
			}).then(res=>{
				console.log(res)
				this.navList = res.data
				
			}).catch(error=>{
				console.log(error)
			})
		},
		lookDetails(item){
			console.log(item)
			uni.navigateTo({
				url:'/pages/order/postSale/postDetails?item='+JSON.stringify(item)
			})
		},
		//订单状态文字和颜色
		orderStateExp(state){
			let stateTip = '',
				stateTipColor = '#fa436a';
			switch(+state){
				case 1:
					stateTip = '待付款'; break;
				case 2:
					stateTip = '待发货'; break;
				case 3:
					stateTip = '待收货'; break;
				case 4:
					stateTip = '待评价'; break;
				case 5:
					stateTip = '已完成'; break;
				case 9:
					stateTip = '订单已关闭'; 
					stateTipColor = '#909399';
					break;
					
				//更多自定义
			}
			return {stateTip, stateTipColor};
		}
	},
	
}
